.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

ul {
  padding: 0;
  list-style: none;
}

.gavno {
  border: 1px solid rgba(20, 20, 20, 0.387)!important;
  opacity: 0.8!important;
}

.hide {
  display: none;
}

.hovered {
  position: relative;
}

.hovered::after {
  content: '';
  display: block;
  position: absolute;
  width: 50px;
  height: 50px;
  opacity: 0.6;
  border-radius: 50%;
  background-color: gray;
box-shadow: inset 4px 4px 6px rgba(105, 92, 92, 0.5), inset -4px -4px 6px rgba(0,0,0,0.5);
}